import React, { useState, useEffect } from 'react'
import { Grid, TextField, Button } from '@material-ui/core'
import { motion, AnimatePresence } from 'framer-motion'
import AnalysisCard from './AnalysisCard.js';
import Analysis from './Analysis.js';
import replaceString from 'react-string-replace';
import poem from '../../poem.json';
import data from './data';
import ClickablePhrase from './ClickablePhrase.js';
import randomColor from 'randomcolor';
import { analysisVariants } from './MainPoemVariants';

export default function MainPoem() {
    let uniqueKey = 0;
    const [poemContent, setPoemContent] = useState("");
    const [currentAnalysisCard, setCurrentAnalysisCard] = useState();
    const [testState, setTestState] = useState(false);
    const parentVariants = {
        enterPage: {
            transition: {staggerChildren: 0.4, delayChildren: 2}
        }
    }
    const childVariants = {
        enterPage: variantColor => ({
            color: variantColor
        })
    }


    const makeAnalysisCard = (analysisObject) => {
        setCurrentAnalysisCard(<AnalysisCard key={analysisObject} analysisObject={analysisObject} />);
    }
    
    useEffect(() => {
        makeAnalysisCard(new Analysis("N/A", "Welcome To My Analysis!", "You can click on any of the colored words to see the analysis for that section!", "Introduction"));
        data.forEach(element => {
            poem.poem = replaceString(poem.poem, element.phrase, (innerText) => {
                uniqueKey++;
                return (<span key={uniqueKey.toString()} onClick={() => makeAnalysisCard(element)}><ClickablePhrase color={randomColor()} variants={childVariants}>{innerText}</ClickablePhrase></span>)
        });
        });
        setPoemContent(poem.poem);
    }, [])

    return (
        <div style={{height: "100vh", width: "100vw"}}>
            <Grid container style={{display: 'flex', flexDirection: 'row', alignItems: 'center', height: '100%'}}>
                <Grid item xs={6} style={{height: '90%', width: '100%', boxShadow: "21px 21px 46px #f5f5f5, -21px -21px 46px #fafafa", margin: '0em 5em', borderRadius: '2em', padding: '1em 2em'}}>
                    <div style={{display: 'flex', flexDirection: 'column', overflow: 'auto', alignItems: 'center', height: '100%'}}>
                        <h2 style={{fontWeight: '600'}}>The Love Song of J. Alfred Prufock</h2>
                    
                        <motion.div style={{fontSize: '1em', whiteSpace: 'pre-line', marginTop: '1em', textAlign: 'center', maxHeight: '100%', overflow: 'auto'}} variants={parentVariants} animate="enterPage">
                            {poemContent}
                        </motion.div>
                    </div>
                </Grid>

                <Grid item xs={4} style={{height: '90%', width: '100%', boxShadow: "21px 21px 46px #f5f5f5, -21px -21px 46px #fafafa", borderRadius: '2em', padding: '1em 2em'}}>
                    <AnimatePresence>
                            {currentAnalysisCard}
                    </AnimatePresence>
                </Grid>
            </Grid>
        </div>
    )
}
