import React from 'react'
import { motion } from 'framer-motion'

export default function ClickablePhrase(props) {
    return (
        <motion.span onClick={props.whenClicked} whileHover={{cursor: 'pointer'}} style={{fontWeight: '800'}} variants={props.variants} custom={props.color}>
            {props.children}
        </motion.span>
    )
}
