import React from 'react'
import { motion } from 'framer-motion'
import { analysisVariants } from './MainPoemVariants'

export default function AnalysisCard(props) {
    const {phrase, title, type, body} = props.analysisObject;

    return (
        <motion.div variants={analysisVariants} initial="popout" animate="popIn" style={{display: "flex", flexDirection: "column", alignItems: 'center'}} exit="popOut">
            <motion.h2 variants={analysisVariants}> {title}</motion.h2>
            <motion.h3 variants={analysisVariants} style={{marginTop: '0', color: '#AAAAAA'}}>{type}</motion.h3>
            <motion.p variants={analysisVariants} style={{marginTop: '1em', lineHeight: '2'}}>{body}</motion.p>
        </motion.div>

    )
}
