import logo from './logo.svg';
import { useState } from 'react';
import './App.css';
import TitleScreen from './components/title_screen/TitleScreen';
import { AnimatePresence, motion } from 'framer-motion';
import MainPoem from './components/poem/MainPoem';

function App() {
  const [mainScreen, setMainScreen] = useState(false)
  return (
    <div className="App">
      <motion.div>
      <AnimatePresence>
                {mainScreen ? 
                (
                <motion.div exit={{opacity: 0}} initial={{opacity: 0}} animate={{opacity: 1}} transition={{delay: 1, when: 'beforeChildren'}} key={"main"}>
                  <MainPoem />
                </motion.div>
                ) :
                (
                  <motion.div exit={{opacity: 0}} key={"title"}>
                    <TitleScreen mainScreen={() => setMainScreen(true)} />
                  </motion.div>
                  )
                }
      </AnimatePresence>
      </motion.div>
    </div>

  );
}

export default App;

